export function sliderInit() {
    $(".slider .slides").each((index, holder) => {
        // Slider Events
        $(holder).on("init", (event, slick, direction) => {
            const slides = $(holder).find(".slide-item");
            const heightsArray = [];
            slides.each(function (index, elem) {
                const elemHeight = $(elem).innerHeight();
                heightsArray.push(elemHeight);
            });
            const maxHeight = Math.max(...heightsArray);
            slides.find(".item-content").each(function (index, elem) {
                $(elem).css({
                    "height": `${maxHeight}px`
                });
            });
        });

        // Slider Init
        $(holder).slick({
            infinite: false,
            slidesToShow: 4,
            slidesToScroll: 1,
            responsive: [
                {
                    breakpoint: 1200,
                    settings: {
                        slidesToShow: 3
                    }
                },
                {
                    breakpoint: 800,
                    settings: {
                        slidesToShow: 2
                    }
                },
                {
                    breakpoint: 500,
                    settings: {
                        slidesToShow: 1
                    }
                }
            ]
        });
    });
}


