import "babel-polyfill";
import "fullpage.js";
import "slick-carousel";
import binder from "./libs/binder";
import { staticFunctions, constants, commonFunction, anotherCommonFunction, navFunction, myUnusedFunction } from "./modules/module";
import { fullpageInit } from "./modules/fullpage/fullpageSettings";
import { slickSupportSection, slickWhyAgTechSection, sliderInit, slickSuccessStories } from "./modules/slick/slickSettings";

binder({
    bounds: {
        "html": [constants, staticFunctions],
        "body": [commonFunction, anotherCommonFunction],
        ".header": navFunction,
        "#fullpage" : fullpageInit,
        ".descr-banner" : slickSupportSection,
        ".why-agtech .items-list" : slickWhyAgTechSection,
        ".slider": sliderInit,
        ".success-stories-wrap .listing": slickSuccessStories
    },
    runTests: true
});



