export function fullpageInit() {
    $(document).ready(function(){
        function fullPageAfterRender() {
            const dots = $("#fp-nav span");

            dots.each(function (index, elem) {
                const elemText = `0${index + 1}`;
                $(elem).append(elemText);
            });

            // $.fn.fullpage.silentMoveTo(1);

        }

        const slides = $("#fullpage .section");

        $("#fullpage").fullpage({
            css3: true,
            navigation: true,
            verticalCentered: false,
            navigationPosition: "left",
            afterRender: function () {
                fullPageAfterRender();
            },

            afterLoad: function (anchorLink, index) {
                $("#fullpage").css({
                    visibility: "visible"
                });

                const self = $(this).find('.animated');

                setTimeout(function(){
                    self.addClass('done')
                }, 1000)
            },

            onLeave: function (index, nextIndex) {
                const navigation = $("#fp-nav");
                if ($(window).innerWidth() > 1200) {
                    nextIndex === slides.length ? navigation.fadeOut(400) : navigation.fadeIn(400);
                }
            },
            responsiveSlides: false,
            responsiveWidth: 1200,
            scrollBar: true,
        });
    })
}


